.investment-section-items .single-item {
  background-color: #2b2a28;
  box-shadow: 1px 2px 20px rgb(0 0 0 / 10%);
  border-radius: 20px;
  margin: 1rem 1rem 2rem 1rem;
  position: relative;
  /* cursor: pointer; */
  transition: all ease-in-out 0.3s;
  min-height: 340px;
  position: relative;
  overflow: hidden;
}

.pp-card-body {
  padding: 24px;
}

.pp-card-top {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.investment-section-items .single-item a {
  text-decoration: none;
  display: block;
  text-align: left;
}

.pp-card-top .icon-box {
  width: 90px;
  height: 90px;
  padding: 18px;
  border-radius: 50%;
  overflow: hidden;
  object-fit: cover;
  position: relative;
}

.pp-card-top .icon-box::before {
  content: "";
  /* background-image: url(../images/choose-icon-bg.png); */
  background-position: center center;
  background-size: cover;
  width: 90px;
  height: 90px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform-origin: center center;
}

.pp-card-top .icon-box span {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  background-color: #e9e9e9;
  /* padding: 5px; */
  overflow: hidden;
}

.pp-card-top .icon-box img {
  width: 100%;
}

.pp-card-top .title-box {
  flex: 1;
  color: #efe9e9;
  text-align: left;
}

.section-text .section-title,
.pp-card-top .title-box h3 {
  color: #fff;
}

.pp-card-top .title-box h3 {
  margin-bottom: 0;
  color: #333;
  font-size: 20px;
  font-weight: bold;
}

.investment-section-items .single-item h3 a div {
  color: #fff;
}

.item-social {
  display: flex;
  margin: 5px 0;
  position: relative;
  z-index: 2;
}

.investment-section-items .single-item a {
  text-decoration: none;
  display: block;
  text-align: left;
}

.pp-card-top .title-box span {
  padding: 3px 10px 4px 10px;
  border-radius: 16px;
  font-size: 12px;
  background-color: rgb(240 185 19 / 26%);
  color: #f1b90c;
}
.status {
  padding: 3px 10px 4px 10px;
  border-radius: 16px;
  font-size: 12px;
  background-color: rgb(240 185 19 / 26%);
  color: #f1b90c;
  margin-left: 10px
}

.item-desc {
  font-size: 16px;
  margin-bottom: 15px;
  padding: 8px 10px;
  min-height: 1.5rem;
  text-align: left;
  color: rgba(255, 255, 255, 0.9);
}

.item-learn-more {
  height: 1.5rem;
  line-height: 1.5;
  display: block;
}

.pp-card-info {
  display: flex;
  padding: 0 10px;
  margin-bottom: 10px;
}

.pp-card-col b {
  color: #fff;
  font-size: 18px;
}

.pp-card-col {
  flex: 1;
  text-align: left;
  font-size: 12px;
  color: #999;
  line-height: 1.3;
}

.pp-card-progress {
  position: relative;
  height: 8px;
  border-radius: 8px;
  width: 100%;
  background-color: rgb(255 255 255);
  margin-top: 5px;
  margin-bottom: 20px;
}

.pp-card-progress-percent {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 8px;
  width: 60%;
  height: 100%;
  background-color: #f1b90c;
  transition: all ease-in-out 0.4s;
}

.pp-card-progress-label,
.pp-card-progress-label b {
  font-size: 12px;
}

.pp-card-progress-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  top: 10px;
  position: absolute;
  width: 100%;
  color: #999;
}

.pp-card-progress-label b {
  color: #fff;
}

.text-end {
  text-align: right !important;
}

.item-social.flex-row {
  display: flex;
  justify-content: space-between;
  padding: 0px 20px 0px 0px;
}

a.text-decoration-none.bg-border {
  background-color: #ffffff12;
  padding: 4px 6px 4px 6px;
  border-radius: 20px;
}

a.text-decoration-none.no-border {
  padding: 4px 6px 4px 6px;
}

.pading-card {
  padding: 10px;
}

